<template>
  <div id="customized-storeList">
        <search-header :hasBack="hasBack" :customized='customized'>
      <!-- <router-link :to="{ name : 'Index'}" slot="icon">
            <div class="message" @click="toMessage">
              <img src="../../assets/common_btn_message@2x.png"/>
              <span class="point">0</span>
            </div>
          </router-link> -->
    </search-header>
    <div class="store-list">
      <div class="list-item" v-for="(item,index) in shopList" :key="index" @click="toGoodList(item)">
        <div class="item-img fl-left">
          <img :src="item.bgImagePath">
          <div class="item-count" v-if="false">3</div>
        </div>
        <div class="item-info fl-left">
          <h4>{{item.orgName}}</h4>
          <p>定制商品数：{{item.hasCustomize}}</p>
        </div>
      </div>
      <!-- <div class="list-item">
        <div class="item-img fl-left">
          <img src="../../assets/pic.png">
          <div class="item-count">3</div>
        </div>
        <div class="item-info fl-left">
          <h4>巴黎欧莱雅官方旗舰店</h4>
          <p>定制商品数：2560</p>
        </div>
      </div>
      <div class="list-item">
        <div class="item-img fl-left">
          <img src="../../assets/pic.png">
          <div class="item-count">13</div>
        </div>
        <div class="item-info fl-left">
          <h4>巴黎欧莱雅官方旗舰店</h4>
          <p>定制商品数：2560</p>
        </div>
      </div> -->

    </div>
  </div>
</template>

<script>
import SearchHeader from "@/components/Header/SearchHeader";

export default {
  data() {
    return {
      hasBack: true,
      customized: true,
      shopList: []
    };
  },
  created() {
    this.getShopList();
  },
  watch: {},
  methods: {
    getShopList() {
      let params = {
        a: 3,
        type: 2,
        categoryId: this.$route.query.id,
        li: 10,
        pa: 1
      };
      // console.log(params);
      this.$Api.getShopList(params).then(res => {
        console.log(res);
        if (res.q.s == 0) {
          this.shopList = res.q.shops;
        }
      });
    },
    toGoodList(item) {
      this.$router.push({
        name: "CustomizedGoodsList",
        query: {
          orgId: item.orgId,
          title: item.orgName
        }
      });
    }
  },
  components: {
    SearchHeader
  }
};
</script>

<style lang="scss">
@import "../../sass/baseSass.scss";

#customized-storeList {
  .store-list {
    background: #fff;

    .list-item {
      width: box(750);
      height: box(220);
      border-bottom: solid #ccc box(1);
      padding: box(30);

      .item-img {
        width: box(220);
        height: box(160);
        position: relative;

        img {
          width: box(220);
          height: box(160);
          border-radius: box(8);
        }
        .item-count {
          min-width: box(30);
          height: box(30);
          background-color: #fe4436;
          box-shadow: 1px 2px 7px 0px rgba(0, 0, 0, 0.35);
          border-radius: box(15);
          position: absolute;
          bottom: box(10);
          right: box(10);
          color: #fff;
          line-height: box(30);
          text-align: center;
          font-size: box(24);
          padding: 0 box(9);
        }
      }

      .item-info {
        width: box(440);
        height: box(160);
        position: relative;
        margin-left: box(30);
        color: #1a1a1a;

        h4 {
          font-size: box(30);
        }
        p {
          font-size: box(28);
          position: absolute;
          bottom: 0;
          left: 0;
        }
      }
    }
  }
}
</style>



